.open-sans {
    font-family: "Open Sans", sans-serif;
}

.roboto {
    font-family: "Roboto", sans-serif;
}

.text-12 {
    font-size: 12px;
}

.text-14 {
    font-size: 14px;
}

.text-16 {
    font-size: 16px;
}

.text-20 {
    font-size: 20px;
}

.text-24 {
    font-size: 24px;
}

.text-26 {
    font-size: 26px;
}

.font-weight-600 {
    font-weight: 600;
}

.font-weight-700 {
    font-weight: 700;
}

.font-weight-800 {
    font-weight: 800;
}

a.nav-link {
    display: flex;
    align-items: center;
}

.container {
    padding-right: 0;
    padding-left: 0;
}

.navbar {
    padding-right: 0;
    padding-left: 0;
}

.text-landing {
    padding-top: 120px;
    padding-bottom: 10px;
}

.text-landing-title {
    padding-top: 50px;
    padding-bottom: 20px;
}

.button {
    border-radius: 30px;
    padding-right: 60px;
    padding-left: 60px;
}

.coklat {
    background-color: #795548;
}

.img-landing {
    padding-top: 50px;
    -webkit-filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.2));
    filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.2));
}

.card-img-top {
    height: 240px;
    width: auto;
}

@media (max-width: 576px) {
    .container {
        padding-right: 15px;
        padding-left: 15px;
    }
    .img-landing {
        padding-top: 15px;
    }
    .text-landing {
        padding-top: 0px;
        padding-bottom: 0px;
        font-size: 36px;
    }
    .text-landing-description {
        font-size: 16px;
    }
    .text-landing-title {
        padding-top: 0px;
        padding-bottom: 10px;
        font-size: 36px;
    }
    .button {
        font-size: 16px
    }
    .card.shadow {
        margin-bottom: 20px
    }
    .card.shadow-sm {
        margin-bottom: 20px
    }
}